<template>
  <div class="main">
    <div class="main-left">
      <div class="logo">logo</div>
      <div class="menu">
        <el-menu
          background-color="#202427"
          text-color="#fff"
          active-text-color="#ffd04b">
          <el-submenu v-for="(menu01, index01) in $store.state.menu" :key="menu01.id" :index="menu01.id + ''">
            <template slot="title">
              <i :class="menu01.icon"></i>
              <span>{{menu01.menu}}</span>
            </template>
            <el-menu-item-group v-for="(menu02, index02) in menu01.nodes" :key="menu02.id">
              <el-menu-item :index="menu02.id + ''"
                @click="selectMenu(menu02)">{{menu02.menu}}</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </div>
    </div>
    <div class="main-right">
      <div class="main-nav">
        <div class="menu-nav">
          首頁
        </div>
        <div class="nav-space"></div>
        <div class="user-wrap">
          <el-dropdown @command="userCommand">
            <span class="el-dropdown-link">
              {{userInfo.username}}<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item><i class="el-icon-edit-outline"></i> 修改密码</el-dropdown-item>
              <el-dropdown-item divided command="loginOut"><i class="el-icon-back"></i> 退出登录</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
        <div class="message">
          <i class="el-icon-bell"></i>
        </div>
      </div>
      <div class="main-page">
        <router-view/>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import Util from '../util/util';
let { setCookie } = Util;
export default {
  name: 'Index',
  data() {
    return {
    }
  },
  mounted() {
    this.$store.dispatch('getMenu');
  },
  computed: {
    ...mapGetters(['userInfo'])
  },
  methods: {
    selectMenu(menu) {
      this.$router.push(menu.path);
    },
    userCommand(command) {
      switch (command) {
        case 'loginOut':
          this.loginOut();
          break;
        default:
          break;
      }
    },
    loginOut() {
      window.localStorage.setItem('userInfo', '');
      setCookie('token', '');
      this.$router.push('/login');
    }
  }
}
</script>

<style lang="less">
.main {
  display: flex;
  width: 100%;
  height: 100%;
  overflow: hidden;
  .main-left {
    flex: 0 0 210px;
    background: #121212;
  }
  .main-right {
    /*这里需要设置一个宽度数值，防止内容里的elementUI table组件最外层无法获取正确100%宽度*/
    /*flex: 1;*/
    width: calc(100% - 210px);
    height: 100%;
  }
}
.logo {
  height: 80px;
  color: #fff;
}
.main-nav {
  display: flex;
  height: 50px;
  line-height: 50px;
  box-shadow: 0 0 10px #000;
}
.menu-nav {
  padding: 0 18px;
  border-right: 1px solid #ddd;
  border-bottom: 3px solid orange;
}
.nav-space {
  flex: 1;
}
.user-wrap {
  padding: 0 18px;
  border-left: 1px solid #ddd;
  cursor: pointer;
}
.message {
  padding: 0 18px;
  border-left: 1px solid #ddd;
  cursor: pointer;
}
.main-page {
  overflow-y: auto;
  height: calc(100% - 50px);
}

.el-menu {
  border-right: 0;
}
</style>



